<template>
<div>
    <div v-if="peleadorEncontrado">
        <h2>Â¡Peleador encontrado!</h2>
        <h3>id: {{id}}</h3>
        <h1> {{nombre}}</h1>
        <img :src="imgSrc" alt="">
    </div>

    <div v-else>
        <h1>El peleador que buscas, no existe ðŸ˜¢</h1>
    </div>
</div>
   
</template>

<script>
import peleadores from "@/peleadores.json" 
export default {
    name: 'component-peleador',
    props: ["id"],
    data (){
        return {
            peleadores,
            peleador:{
                id:"",
            },
            nombre:"",
            imgSrc:""
        }
        
    },
    computed: {
        peleadorEncontrado(){
            return this.peleadores.find((peleador)=> peleador.id === this.id)
        } 
    },
    methods: {
        FindPeleador(){
            if (this.peleadorEncontrado === undefined){
                alert("el peleador que buscas no existe ðŸ˜¢")
                return
            }
            this.peleador.id = this.peleadorEncontrado.id
            this.nombre = this.peleadorEncontrado.nombre
            this.imgSrc = this.peleadorEncontrado.imgSrc
        }
    },
    // watch: {},
    // components: {},
    // mixins: [],
    // filters: {},
    // -- Lifecycle Methods
    created(){
        this.FindPeleador()
    }
    // -- End Lifecycle Methods
}
</script>

<style scoped>
    img{
        width: 300px;
    }
</style>